<template>
  <div class="contact bg-light">
    <h1 class="text-center pt-7 h2"><span>Contact Us</span></h1>
    <v-container>
      <v-row>
        <v-col cols="12" md="6">
          <v-card>
            <v-card-text>
              <v-form>
                <v-radio-group row>
                  <v-radio label="General Enquiry" value="1"></v-radio>
                  <v-radio label="Business Listing" value="2"></v-radio>
                  <v-radio label="Reseller Issue" value="3"></v-radio>
                </v-radio-group>

                <v-text-field label="Name" required></v-text-field>
                <v-text-field label="Email" required></v-text-field>
                <v-text-field label="Contact" required></v-text-field>
                <v-text-field label="City" required></v-text-field>
                <v-textarea label="Message" required></v-textarea>

                <v-row align="center" class="my-2">
                  <v-col cols="3" md="2"><input type="number" class="form-control" readonly required :value="r1"></v-col>
                  <v-col cols="1" md="1"><v-icon class="" size="20">mdi-plus</v-icon></v-col>
                  <v-col cols="3" md="2"><input type="number" class="form-control" :value="r2" solo readonly required></v-col>
                  <v-col cols="1" md="1"><v-icon class="" size="20">mdi-equal</v-icon></v-col>
                  <v-col cols="4" md="2"><input type="number" class="form-control" v-model="total" solo required></v-col>
                  <v-col cols="12" md="4" class="py-0"><p class="link-dark">Are you human, or spambot?</p></v-col>
                </v-row>

                <v-btn @click="result()" color="cred">Send</v-btn>
              </v-form>
            </v-card-text>
          </v-card>

        </v-col>
        <v-col cols="12" md="3">
          <h5>Find Reseller<br>
            (Auth. Service Dealers)</h5>
          <v-autocomplete
              prepend-inner-icon="mdi-city"
              dense>
          </v-autocomplete>
        </v-col>
        <v-col cols="12" md="3">
          <p>{{ getadress.address }}</p>
          <p>
            <strong class="me-1">Contact:</strong>
            <span class="cred">{{ getadress.contact_number }}</span>
          </p>
        </v-col>
      </v-row>
    </v-container>
  </div>
</template>

<script>
import axios from "axios";

export default {
  name: "ContactView",
  metaInfo: {title: 'Contact Us'},
  data(){
    return{
      getadress: [],
      r1: '',
      r2: '',
      r3: '',
      total: '',
    }
  },

  methods:{
    getadresss(){
      axios.get('profile/details')
          .then((resp)=>{
            this.getadress = resp.data.profile_details;
          });
    },
    myFunction() {
      this.r1 = Math.floor(Math.random()*10);
      this.r2 = Math.floor(Math.random()*10);
    },
    mytotal(){
      this.r3 = this.r1 + this.r2;
    },
    result(){
      if(this.total != this.r3 || this.total == null){
        alert('Please fill the correct captcha');
      }
    },
  },
  mounted() {
  this.getadresss();
  this.myFunction();
  this.mytotal();
  }
}
</script>

<style scoped>

</style>